import React from "react";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
var size = window.innerWidth;
class Carousel extends React.Component {

  getSize() {
    var responsive = {};

    if (size > 1000) {
      return (responsive = {
        0: { items: 1 },
        1: { items: 2 }
      })
    }
    else {
      return (responsive = {
        0: { items: 1 }
      })

    }
  }

  render() {


    const handleOnDragStart = e => e.preventDefault();
    return (

      <AliceCarousel
        autoPlay={true}
        responsive={this.getSize()}
        fadeOutAnimation={true}
        mouseDragEnabled={true}
        buttonsDisabled={true}
        dotsDisabled={true}
        autoPlayInterval={1000}
      >
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/GnU0nRv.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/qKYLU8q.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/Pq0rmb1.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/g4qKYs7.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/NvgH63H.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://imgur.com/yT0NYSL.jpg"
          onDragStart={handleOnDragStart}
        />
      </AliceCarousel>
    );
  }
}
export default Carousel;
