import React, { Component } from 'react'
import { FormattedMessage } from 'react-intl';
import { FormatTime } from "../Scripts/time"

export default class FasterMale extends Component {
    checkZeros(value) {
        if (value==="00:00:00") {
          return ("-")
        }
        else {
          return (value)
        }
      }
      checkShowNameZeros(value, time) {
        if (time==="00:00:00") {
          return ("-")
        }
        else {
          return (value)
        }
      }
    render() {
        var Array = this.props.athleteList.filter(function (el) {
            return el.sexo ==="M"
                   
          });
        //get the faster time
        let fasterTime = Array.map(athelete => {
            return athelete.acumulated_time;
        });
        //get the faster athlete name
        let fasterName = Array.map(athelete => {
            return athelete.name;
        });

        return (

            <div>
                {/* Info boxes */}
                <div className="col-md-3 col-sm-6 col-xs-12">
                    <div className="info-box">
                        <span className="info-box-icon bg-blue"><i className="fa fa-fw fa-male" /></span>
                        <div className="info-box-content">
                            <span className="info-box-text"><FormattedMessage id="FASTER" /></span>
                            <span className="info-box-text"> <strong>{ (fasterTime[0])?this.checkShowNameZeros(fasterName[0],FormatTime(fasterTime[0])):""} </strong></span>
                            <span className="info-box-number"> <small>{(fasterTime[0])?this.checkZeros(FormatTime(fasterTime[0])):""}</small></span>
                        </div>
                        {/* /.info-box-content */}
                    </div>
                    {/* /.info-box */}
                </div>

            </div>
        )
    }
}
