import React, { Component } from "react";
import ReactTable from "react-table";
import { FormatTime } from "../Scripts/time"
import { calculatePagination } from "../Scripts/PaginationHelper"
import { FormattedMessage } from 'react-intl';
import './blink_me.css';
var size = window.innerWidth;

export default class TableGlobal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }


  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }
  }

  checkZerosPos(value) {
    if (parseInt(value)===0) {
      return ("-")
    }
    else {
      return (value)
    }
  }

  checkZerosTime(value, distance, prediction, position) {
    
    if(prediction==="YES" || position===1)
    {
      return (value)
    }
    if(parseInt(distance) != 0)
    {
      if(value==="00:00:00"){
        return("-")
      }
      else {
        return (value)
      }
    }
    else
    {
      return (value)
    }


  }

  CalculateKMperH(checkpointName, time) {
    var distance
    this.props.checkpoint.map(checkpoint => {
      if (checkpoint.checkpoint.local === checkpointName) {
        distance = checkpoint.distancia_acumulada
      }
      return null
    })
    var km = distance / 1000;
    var TimeSplit = time.split(":")
    var H = TimeSplit[0]
    var M = TimeSplit[1]
    var S = TimeSplit[2]
    var finaltime = parseFloat(H) + parseFloat((M / 60)) + parseFloat((S / 3600))
    var KMperH = parseFloat(km) / parseFloat(finaltime)
    if (isNaN(KMperH)) {
      KMperH = 0;
    }
    if (KMperH === Infinity) {
      KMperH = 0;
    }
    return (KMperH.toFixed(2))
  }

  CalculateMINperKM(checkpointName, time) {
    var MInperKM = parseFloat(60 / this.CalculateKMperH(checkpointName, time))
    if (!isFinite(MInperKM)) {
      MInperKM = 0;
    }
    if (isNaN(MInperKM)) {
      MInperKM = 0;
    }
    if (MInperKM === Infinity) {
      MInperKM = 0;
    }
    return (MInperKM.toFixed(2));
    

  }

  getDistance(checkpointName) {
    var distance

    this.props.checkpoint.map(checkpoint => {
      if (checkpoint.checkpoint.local === checkpointName) {
        distance = checkpoint.distancia_acumulada
      }
      return null
    })
    distance = parseFloat(distance / 1000);
    return (distance.toFixed(2) + " km")

  }
  getaltitude(checkpointName) {
    var Altitude

    this.props.checkpoint.map(checkpoint => {
      if (checkpoint.checkpoint.local === checkpointName) {
        Altitude = checkpoint.checkpoint.altitude
      }
      return null
    })
    return (Altitude + " m")

  }
  ReturnPredictions(prediction, time, state) {
    if (state == "EP") {
      if (prediction === "NO") {
        return (<span>{time}</span>)
      }
      else {
        return (
          <div  >
            <span class="color">{time}</span>
            <br />
            <span class="blink_me"><FormattedMessage id="FORECAST" /></span>
          </div>
        )


      }
    }
    else {
      return (<span>-</span>)
    }

  }
  render() {

    //Info for the filters

    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        show: false
      },
      {
        Header: "prediction",
        accessor: "prediction",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        show: false
      },
      {
        Header: "time_status",
        accessor: "time_status",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        show: false
      },
      {
        Header: <FormattedMessage id="GERAL" />,
        width: (size > 800) ? 70 : 50,

        accessor: "PositionGlobal",
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {row.original.prediction == "NO" ? this.checkZerosPos(row.value) : "-"} </span>
        ),

      },
      {
        Header: <FormattedMessage id="GENDER" />,
        width: (size > 800) ? 70 : 50,

        accessor: "PositionGender",
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {row.original.prediction == "NO" ? this.checkZerosPos(row.value) : "-"} </span>
        ),

      },
      {
        Header: <FormattedMessage id="CATEGORY" />,
        width: (size > 800) ? 70 : 60,

        accessor: "PositionTier",
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {row.original.prediction == "NO" ? this.checkZerosPos(row.value) : "-"} </span>
        ),

      },

      {
        Header: <FormattedMessage id="PLACE" />,
        accessor: "local",
        filterable: false,
        sortable: false,
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value

      },
      {
        Header: <FormattedMessage id="TIME" />,
        accessor: "acumulated_time",
        filterable: false,
        sortable: false,
        maxWidth: (size > 800) ? 200 : 70,
        style: {
          textAlign: "Center"
        },
        Cell: row => this.ReturnPredictions(row.original.prediction, this.checkZerosTime(FormatTime(row.value),this.getDistance(row.original.local), row.original.prediction,row.original.PositionGlobal) , row.original.time_status)

      },
      {
        Header: "Dif 1º",
        accessor: "dif",
        filterable: false,
        sortable: false,
        maxWidth: (size > 800) ? 200 : 70,
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {this.checkZerosTime(FormatTime(row.value), this.getDistance(row.original.local),"NO",row.original.PositionGlobal)} </span>
        )
      },
      {
        Header: <FormattedMessage id="HEIGHT" />,
        filterable: false,
        sortable: false,
        show: this.CheckSize(),
        maxWidth: 90,

        style: {
          textAlign: "Center"
        },
        Cell: row => this.getaltitude(row.original.local)
      },
      {
        Header: <FormattedMessage id="DISTANCE" />,
        filterable: false,
        sortable: false,
        show: this.CheckSize(),
        maxWidth: 90,
        style: {
          textAlign: "Center"
        },
        Cell: row => this.getDistance(row.original.local)
      },
      {
        Header: "KM/H",
        filterable: false,
        sortable: false,
        maxWidth: (size > 800) ? 60 : 50,

        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {row.original.prediction == "NO" && row.original.time_status == "EP" ? this.checkZerosPos(this.CalculateKMperH(row.original.local, row.original.acumulated_time)) : "-"} </span>
        )
      },
      {
        Header: "MIN/KM",
        filterable: false,
        sortable: false,
        maxWidth: (size > 800) ? 60 : 55,

        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {row.original.prediction == "NO" && row.original.time_status == "EP" ? this.checkZerosPos(this.CalculateMINperKM(row.original.local, row.original.acumulated_time)) : "-"} </span>

        )
      }
    ];
    return (
      <ReactTable
        columns={columns}
        data={this.props.athleteList}
        filterable={false}
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={parseInt(this.props.checkpoint.length)}
        showPagination={false}
        showPaginationTop={false}
        showPaginationBottom={false}
        showPageSizeOptions={false}
        showPageJump={false}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={false}
        multiSort={false}
        resizable={false}
        defaultSortDesc={false}
        pageText={<FormattedMessage id="PAGE" />}
        rowsText={""}
        className="-striped -highlight"
      />
    );
  }
}
