import React, { Component } from "react";
import "../../AppHome.css";
import "../../App.css";
import "../../AdminLTE.css";
import ListTrails from "./trails";
import Carousel from "../structure/carousel";
import NavBar from "../structure/HeaderHome";
import FooterPage from "../structure/footerHome";
import { Button } from "reactstrap";
import Loader from "../Loader/Loader";
import { FormattedMessage } from 'react-intl';
import ReactGA from 'react-ga';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`
const TOKEN = API_KEY;


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      trails: [],
      showMore: false,
      loading: false,
    };
    this.getTrailEvents();
  }


  /*show recent trails events */
  getTrailEvents = async () => {
    const API_CALL = await fetch(
      `${API_URL}/trail/EventRecents/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    try {
      localStorage.setItem(`trailsListRecent`, JSON.stringify(data))
    }
    catch {
      localStorage.clear();
    }
    this.setState({ trails: data, showMore: false, loading: true });
  };

  /*show all trails events */
  getALLTrailEvents = async () => {
    const API_CALL = await fetch(
      `${API_URL}/trail/Event/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    try {
      localStorage.setItem(`trailsList`, JSON.stringify(data))
    }
    catch {
      localStorage.clear();
    }
    this.setState({ trails: data, showMore: true, loading: true });
    window.scrollTo(0, 300)

  };
  componentWillMount() {
    //CACHES
    try {
      if (localStorage.getItem(`trailsListRecent`)) {
        const trailsData = JSON.parse(localStorage.getItem(`trailsListRecent`))
        this.setState({ trails: trailsData, loading: true })
      }
    }
    catch {

    }
    //END CACHE
    this.getTrailEvents()

  }


  /*used to check if user click button, hide and show more trails */
  checkShow = () => {
    if (this.state.showMore) {
      try {
        if (localStorage.getItem(`trailsListRecent`)) {
          const trailsData = JSON.parse(localStorage.getItem(`trailsListRecent`))
          this.setState({ trails: trailsData, loading: true })
        }
      }
      catch {
  
      }
      this.getTrailEvents();
    } else {
      try {
        if (localStorage.getItem(`trailsList`)) {
          const trailsData = JSON.parse(localStorage.getItem(`trailsList`))
          this.setState({ trails: trailsData, loading: true })
          console.log(trailsData)
        }
      }
      catch {
  
      }
      this.getALLTrailEvents();
    }
  }

  /*Loading message for the checkpoints*/
  LoadingAthletes() {
    if (this.state.loading === true) {
      return (

        <ListTrails trails={this.state.trails} />

      )
    }
    else {
      return (
        <Loader />
      )
    }
  }

  render() {

    return (
      <div style={{ backgroundColor: "White" }} className="App">
        <NavBar />
        <Carousel />
        {/* Listar os trails e imagens*/}
        {this.LoadingAthletes()}
        {/* Listar todos os trails */}
        <Button
          onClick={this.checkShow}
          style={{ background: "#3c8dbc", color: "white" }}
        >
          {this.state.showMore === false ? <FormattedMessage id="BUTTONHOMEMORE" /> : <FormattedMessage id="BUTTONHOMELESS" />}
        </Button>{" "}

        <FooterPage />

      </div>
    );
  }
}

export default App;