import React, { Component } from 'react'
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableGlobal from "../Tables/TableGlobal"
import Loader from "../Loader/Loader";
import ChartVelocity from "../Chart/Velocity"
import ChartPosition from "../Chart/Position"
import PositonWidget from "../Widgets/Position"
import LoadingWidget from "../Widgets/Loading"
import TimeWidget from '../Widgets/Time'
import VelocityWidget from '../Widgets/velocity';
import PlaceWidget from '../Widgets/LastPlace';
import FavoriteChecker from "../favorites/favoritesChecker"
import { FormattedMessage } from 'react-intl';
import { FormatTime } from "../Scripts/time"

const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
const MICROSERVICE_PDFS = `${process.env.REACT_APP_PUBLIC_MICROSERVICE_PDFS}`;

//for the charts
var LastCheckpointID = 0;
var Faster = [];
var FasterID = 0;
export default class AthleteDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadingGlobal: false,
            loadingTier: false,
            loadingGender: false,
            loadingCheckpoint: false,
            loadingList: false,
            loadingFaster: false,
            loadingSlow: false,
            loadingAverage: false,
            ListGlobal: [],
            ListFaster: [],
            ListSlow: [],
            ListAverage: [],
            PersonalInfo: [],
            athleteList: [],
            athleteListStatus: [],
            CheckpointInfo: [],
            AthleteID: this.props.match.params.AthleteID,
        };

    }

    componentWillMount() {
        //CACHES
        try {
            if (localStorage.getItem(`ListGlobal${this.props.match.params.EventID}${this.props.match.params.AthleteID}`)) {
                const ListGlobal = JSON.parse(localStorage.getItem(`ListGlobal${this.props.match.params.EventID}${this.props.match.params.AthleteID}`))
                this.setState({ ListGlobal: ListGlobal, loadingGlobal: true })
            }
            if (localStorage.getItem(`PersonalInfo${this.props.match.params.EventID}${this.props.match.params.AthleteID}`)) {
                const PersonalInfo = JSON.parse(localStorage.getItem(`PersonalInfo${this.props.match.params.EventID}${this.props.match.params.AthleteID}`))
                this.setState({ PersonalInfo: PersonalInfo })
            }
            if (localStorage.getItem(`ListFaster${this.props.match.params.CompID}`)) {
                const ListFaster = JSON.parse(localStorage.getItem(`ListFaster${this.props.match.params.CompID}`))
                this.setState({ ListFaster: ListFaster, loadingFaster: true })
            }
            if (localStorage.getItem(`ListSlow${this.props.match.params.CompID}`)) {
                const ListFaster = JSON.parse(localStorage.getItem(`ListSlow${this.props.match.params.CompID}`))
                this.setState({ ListSlow: ListFaster, loadingSlow: true })
            }
            if (localStorage.getItem(`ListAverage${this.props.match.params.CompID}`)) {
                const ListFaster = JSON.parse(localStorage.getItem(`ListAverage${this.props.match.params.CompID}`))
                this.setState({ ListAverage: ListFaster, loadingAverage: true })
            }
            if (localStorage.getItem(`CheckpointInfo${this.props.match.params.CompID}`)) {
                const CheckpointInfo = JSON.parse(localStorage.getItem(`CheckpointInfo${this.props.match.params.CompID}`))
                this.setState({ CheckpointInfo: CheckpointInfo, loadingCheckpoint: true })
            }
            if (localStorage.getItem(`athleteListStatus${this.props.match.params.CompID}${this.props.match.params.AthleteID}`)) {
                const athleteListStatus = JSON.parse(localStorage.getItem(`athleteListStatus${this.props.match.params.CompID}${this.props.match.params.AthleteID}`))
                this.setState({ athleteListStatus: athleteListStatus, loadingList: true })
            }

        }
        catch{

        }
        //END CACHE
        this.GetListGlobal();
        this.GetCheckpointInfo();
        this.GetListGlobalFaster();
        this.GetListGlobalSlow();
        this.GetListGlobalAverage();

    }

    GetListGlobal = async e => {
        const API_CALL = await fetch(`${API_URL}/results/AthTimesWithPositions/${this.props.match.params.CompID}/${this.props.match.params.AthleteID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        const dataSlice = data.slice(0, 1); //Get the personal info
        try {
            localStorage.setItem(`ListGlobal${this.props.match.params.EventID}${this.props.match.params.AthleteID}`, JSON.stringify(data))
            localStorage.setItem(`PersonalInfo${this.props.match.params.EventID}${this.props.match.params.AthleteID}`, JSON.stringify(dataSlice))
        }
        catch{
            localStorage.clear();
        }
        this.setState({ ListGlobal: data, loadingGlobal: true, PersonalInfo: dataSlice });
    };

    GetListGlobalFaster = async e => {
        const API_CALL = await fetch(`${API_URL}/results/faster/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        try {
            localStorage.setItem(`ListFaster${this.props.match.params.CompID}`, JSON.stringify(data))
        }
        catch{
            localStorage.clear();
        }
        this.setState({ ListFaster: data, loadingFaster: true });

    };
    GetListGlobalSlow = async e => {
        const API_CALL = await fetch(`${API_URL}/results/slow/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        try {
            localStorage.setItem(`ListSlow${this.props.match.params.CompID}`, JSON.stringify(data))
        }
        catch{
            localStorage.clear();
        }
        this.setState({ ListSlow: data, loadingSlow: true });

    };
    GetListGlobalAverage = async e => {
        const API_CALL = await fetch(`${API_URL}/results/average/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        try {
            localStorage.setItem(`ListAverage${this.props.match.params.CompID}`, JSON.stringify(data))
        }
        catch{
            localStorage.clear();
        }
        this.setState({ ListAverage: data, loadingAverage: true });

    };
    GetCheckpointInfo = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/Checkpoints/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        try {
            localStorage.setItem(`CheckpointInfo${this.props.match.params.CompID}`, JSON.stringify(data))

        }
        catch{
            localStorage.clear();
        }
        this.setState({ CheckpointInfo: data, loadingCheckpoint: true });
    };

    getAthletsList = async e => {

        const API_CALL = await fetch(`${API_URL}/competition/AthInfo/${this.props.match.params.CompID}/${this.props.match.params.AthleteID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        data.map(Athlete => {
            if (Athlete.athlete.athlete_id === parseInt(this.state.AthleteID)) {
                try {
                    localStorage.setItem(`athleteListStatus${this.props.match.params.CompID}${this.props.match.params.AthleteID}`, JSON.stringify(Athlete))
                }
                catch{
                    localStorage.clear();

                }
                this.setState({ athleteListStatus: Athlete, loadingList: true });
            }
            return null;

        })

    };


    tableGlobal() {
        if (this.state.loadingGlobal === false) {
            return (<Loader />)
        } else {
            if (this.state.ListGlobal.length > 0) {
                //Calculate the data for the arrays
                return (
                    <TableGlobal athleteList={this.state.ListGlobal} checkpoint={this.state.CheckpointInfo} />
                )
            }
            else {
                return (<p style={{ textAlign: "center" }}><FormattedMessage id="NODATA" /></p>)
            }
        }

    }

    ChartVelocityKMRender() {
        if (this.state.loadingGlobal === false) {
            return (<Loader />)
        } else {
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                if (this.state.ListGlobal.length > 0 && !!(this.state.athleteListStatus.athlete.name)) {
                    return (
                        <FormattedMessage id="SLOWER">
                            {(LanguageSlow) => (
                                <FormattedMessage id="AVERAGE_SPEED">
                                    {(LanguageAverage) => (

                                        <FormattedMessage id="FASTER">
                                            {(LanguageFaster) => (
                                                <ChartVelocity checkpoint={this.state.ListGlobal} LanguageFaster={LanguageFaster} LanguageSlow={LanguageSlow} LanguageAverage={LanguageAverage} Name={this.state.athleteListStatus.athlete.name} Slow={this.state.ListSlow.map(data => this.CalculateKMperH(data.Local, data.Tempo))} Average={this.state.ListAverage.map(data => this.CalculateKMperH(data.Local, data.Tempo))} Faster={this.state.ListFaster.map(data => this.CalculateKMperH(data.Local, data.Tempo))} Normal={this.state.ListGlobal.map(data => this.CalculateKMperH(data.local, data.acumulated_time))} />
                                            )}
                                        </FormattedMessage>
                                    )}
                                </FormattedMessage>
                            )}

                        </FormattedMessage>
                    )
                }
                else {
                    return (<p><FormattedMessage id="NODATA" /></p>)
                }
            }


        }
    }

    ChartVelocityMINRender() {
        if (this.state.loadingGlobal === false) {
            return (<Loader />)
        } else {
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                if (this.state.ListGlobal.length > 0 && !!(this.state.athleteListStatus.athlete.name)) {
                    return (
                        <FormattedMessage id="SLOWER">
                            {(LanguageSlow) => (
                                <FormattedMessage id="AVERAGE_SPEED">
                                    {(LanguageAverage) => (

                                        <FormattedMessage id="FASTER">
                                            {(LanguageFaster) => (
                                                <ChartVelocity LanguageFaster={LanguageFaster} LanguageSlow={LanguageSlow} LanguageAverage={LanguageAverage} checkpoint={this.state.ListGlobal} Name={this.state.athleteListStatus.athlete.name} Slow={this.state.ListSlow.map(data => this.CalculateMINperKM(data.Local, data.Tempo))} Average={this.state.ListAverage.map(data => this.CalculateMINperKM(data.Local, data.Tempo))} Faster={this.state.ListFaster.map(data => this.CalculateMINperKM(data.Local, data.Tempo))} Normal={this.state.ListGlobal.map(data => this.CalculateMINperKM(data.local, data.acumulated_time))} />
                                            )}
                                        </FormattedMessage>
                                    )}
                                </FormattedMessage>
                            )}

                        </FormattedMessage>

                    )
                }
                else {
                    return (<p><FormattedMessage id="NODATA" /></p>)
                }
            }


        }
    }

    ChartPositionRender() {
        if (this.state.loadingGlobal === false && this.state.loadingFaster === false) {
            return (<Loader />)
        } else {
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                if (this.state.ListGlobal.length > 0 && this.state.ListGlobal.length > 0 && this.state.ListGlobal.length > 0) {
                    //Calculate the data for the arrays
                    var global = []
                    var Tier = []
                    var gender = []

                    this.state.ListGlobal.map(data => global.push(data.PositionGlobal))
                    this.state.ListGlobal.map(data => Tier.push(data.PositionTier))
                    this.state.ListGlobal.map(data => gender.push(data.PositionGender))

                    return (
                        <FormattedMessage id="CATEGORY">
                            {(LANGUAGECATEGORY) => (
                                <FormattedMessage id="GENDER">
                                    {(LANGUAGEGENDER) => (

                                        <FormattedMessage id="GERAL">
                                            {(LANGUAGEGERAL) => (
                                                <ChartPosition LANGUAGECATEGORY={LANGUAGECATEGORY} LANGUAGEGENDER={LANGUAGEGENDER} LANGUAGEGERAL={LANGUAGEGERAL} checkpoint={this.state.ListGlobal} Global={global} Tier={Tier} Gender={gender} />
                                            )}
                                        </FormattedMessage>
                                    )}
                                </FormattedMessage>
                            )}

                        </FormattedMessage>

                    )
                }
                else {
                    return (<p><FormattedMessage id="NODATA" /></p>)
                }
            }

        }
    }

    componentDidMount() {
        this.getAthletsList()

    }

    getStatus() {

        if (this.state.athleteListStatus.inscription_status_code === "FIN") {
            return (<p style={{ color: "#3c8dbc" }} className="text-muted text-center"> <strong><FormattedMessage id="FIN" /></strong></p>);
        } else if (this.state.athleteListStatus.inscription_status_code === "EP") {
            return (<p style={{ color: "#00a65a" }} className="text-muted text-center"> <strong><FormattedMessage id="EP" /></strong></p>);
        } else if (this.state.athleteListStatus.inscription_status_code === "DNS") {
            return (<p style={{ color: "#f39c12" }} className="text-muted text-center"> <strong><FormattedMessage id="DNS" /></strong></p>);
        } else if (this.state.athleteListStatus.inscription_status_code === "D") {
            return (<p style={{ color: "#f39c12" }} className="text-muted text-center"> <strong><FormattedMessage id="D" /></strong></p>);
        } else if (this.state.athleteListStatus.inscription_status_code === "DNF") {
            return (<p style={{ color: "#dd4b39" }} className="text-muted text-center"> <strong><FormattedMessage id="DNF" /></strong></p>);
        } else if (this.state.athleteListStatus.inscription_status_code === "DSQ") {
            return (<p style={{ color: "#dd4b39" }} className="text-muted text-center"> <strong><FormattedMessage id="DSQ" /></strong></p>);
        }
    }

    CalculateKMperH(checkpointName, time) {
        if (time) {
            var distance
            this.state.CheckpointInfo.map(checkpoint => {
                if (checkpoint.checkpoint.local === checkpointName) {
                    distance = checkpoint.distancia_acumulada
                }
                return null;
            })
            var km = distance / 1000;

            var TimeSplit = time.split(":")
            var H = TimeSplit[0]
            var M = TimeSplit[1]
            var S = TimeSplit[2]
            var finaltime = parseFloat(H) + parseFloat((M / 60)) + parseFloat((S / 3600))
            var KMperH = parseFloat(km) / parseFloat(finaltime)
            if (isNaN(KMperH)) {
                KMperH = 0;
            }
            if (KMperH === Infinity) {
                KMperH = 0;
            }
            return (KMperH.toFixed(2));
        }

    }

    CalculateMINperKM(checkpointName, time) {
        if (time) {
            var MInperKM = parseFloat(60 / this.CalculateKMperH(checkpointName, time))
            if (!isFinite(MInperKM)) {
                MInperKM = 0;
            }
            if (isNaN(MInperKM)) {
                MInperKM = 0;
            }
            if (MInperKM === Infinity) {
                MInperKM = 0;
            }
            return (MInperKM.toFixed(2));
        }


    }

    PositionWidgetRender() {
        if (!!(this.state.ListGlobal[this.GetLastPosition()])) //check if is undefined, is because is awaiting for the api response
        {
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                return (
                    <div>
                        <PositonWidget color={"aqua"} positionGlobal={(this.state.ListGlobal[this.GetLastPosition()].PositionGlobal) === 0 ? "-" : this.state.ListGlobal[this.GetLastPosition()].PositionGlobal} positionTier={(this.state.ListGlobal[this.GetLastPosition()].PositionTier) === 0 ? "-" : this.state.ListGlobal[this.GetLastPosition()].PositionTier} positionGender={(this.state.ListGlobal[this.GetLastPosition()].PositionGender) === 0 ? "-" : this.state.ListGlobal[this.GetLastPosition()].PositionGender} />
                    </div>
                )
            }

        }
        else
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                return (
                    < LoadingWidget color={"aqua"} text={< FormattedMessage id="LOADING" />} />
                )
            }

    }

    GetLastPosition() {
        var position = 0;
        this.state.ListGlobal.forEach(element => {
            if (element.PositionGlobal > 0) {
                position = position + 1;
            }
        })
        return (position)
    }

    TimeVelocityWidgetRender() {

        if (!!(this.state.ListGlobal[this.GetLastPosition()])) //check if is undefined, is because is awaiting for the api response
        {
            var KM = 0;
            var MIN = 0;
            this.state.ListGlobal.map(data => {
                if (parseFloat(this.CalculateKMperH(data.local, data.acumulated_time)) !== 0) {
                    KM = KM + parseFloat(this.CalculateKMperH(data.local, data.acumulated_time))
                    console.log(KM)
                }
                return null
            })
            this.state.ListGlobal.map(data => {
                if (parseFloat(this.CalculateMINperKM(data.local, data.acumulated_time)) !== 0) {
                    MIN = MIN + parseFloat(this.CalculateMINperKM(data.local, data.acumulated_time))
                }
                return null
            })

            var AvarageKM = (KM / (this.GetLastPosition())).toFixed(2)
            var AvarageMIN = (MIN / (this.GetLastPosition())).toFixed(2)
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {

                return (
                    <div>
                        <TimeWidget time={FormatTime(this.state.ListGlobal[this.GetLastPosition()].acumulated_time)} name={<FormattedMessage id="TIME" />} color={"yellow"} />
                        <VelocityWidget name={<FormattedMessage id="AVERAGESPEED" />} speedKM={isNaN(AvarageKM) ? "-" : AvarageKM + " KM/H"} speedMIN={isNaN(AvarageMIN) ? "-" : AvarageMIN + " MIN/KM"} color={"red"} />
                        <PlaceWidget place={this.state.ListGlobal[this.GetLastPosition()].local} color={"green"} />
                    </div>

                )
            }
        }
        else {
            if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {
                return (
                    <div>

                        < LoadingWidget color={"yellow"} text={< FormattedMessage id="LOADING" />} />
                        < LoadingWidget color={"red"} text={< FormattedMessage id="LOADING" />} />
                        < LoadingWidget color={"green"} text={< FormattedMessage id="LOADING" />} />
                    </div>
                )
            }

        }
    }

    chartsRender() {
        if (this.state.athleteListStatus.inscription_status_code === "FIN" || this.state.athleteListStatus.inscription_status_code === "EP") {

            return (
                <div style={{ float: 'right' }} className="col-md-9">
                    <div className="nav-tabs-custom" >
                        <ul className="nav nav-tabs">
                            <li className="active"><a href="#VelocityKMperH" data-toggle="tab">< FormattedMessage id="SPEED" /> KM/H</a></li>
                            <li ><a href="#VelocityMINperH" data-toggle="tab">< FormattedMessage id="SPEED" /> MIN/KM</a></li>
                            <li><a href="#Position" data-toggle="tab">< FormattedMessage id="POSITION" /></a></li>
                        </ul>
                        <div className="tab-content">
                            <div className="active tab-pane" id="VelocityKMperH">
                                {/* Post */}
                                <div className="post">
                                    <div className="chart">
                                        {this.ChartVelocityKMRender()}
                                    </div>
                                </div>
                                {/* /.post */}
                            </div>
                            <div className="tab-pane" id="VelocityMINperH">
                                {/* Post */}
                                <div className="post">
                                    <div className="chart">
                                        {this.ChartVelocityMINRender()}
                                    </div>
                                </div>
                                {/* /.post */}
                            </div>
                            {/* /.tab-pane */}
                            <div className="tab-pane" id="Position">
                                <div className="post">
                                    <div className="chart">
                                        {this.ChartPositionRender()}
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* /.tab-content */}
                    </div>
                    {/* /.nav-tabs-custom */}
                </div>

            )
        }


    }

    getLastCheckpoint() {
        var LastPositon = (this.state.ListGlobal.slice(-1).pop())
        if (!!(LastPositon)) {
            this.state.CheckpointInfo.map(checkpoint => {
                if (checkpoint.checkpoint.local === LastPositon.local) {
                    if (LastCheckpointID === 0) {
                        LastCheckpointID = checkpoint.checkpoint.checkpoint_id
                        //FASTER
                    }

                }
                return null
            }
            )
        }
    }

    showCertificateButton() {

        if (!!(this.state.athleteListStatus.athlete)) {

            if (this.state.athleteListStatus.competition.certification === 1 && this.state.athleteListStatus.inscription_status_code === "FIN") {
                if (!!(this.state.ListGlobal[this.state.ListGlobal.length - 1])) {
                    return (
                        <div>
                            <br></br>
                            <button href="##" className="btn btn-success  btn-block" onClick={() => this.getCertificate()}><b>< FormattedMessage id="CERTIFICATE" /></b></button>
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            <br></br>

                            <button disabled className="btn btn-success  btn-block" ><b> <i class="fa fa-refresh fa-spin"></i> < FormattedMessage id="CERTIFICATE" /></b>
                            </button>

                        </div>)
                }
            }

        }
    }

    getCertificate() {
        if (!!(this.state.athleteListStatus.athlete) && !!(this.state.ListGlobal[this.state.ListGlobal.length - 1])) {

            var ID_Template = this.state.athleteListStatus.competition.cert_tpl
            var Codigo_Validacao = this.state.athleteListStatus.competition.cert_code
            var Nome_Atleta = encodeURIComponent(this.state.athleteListStatus.athlete.name)
            var Sexo = this.state.athleteListStatus.athlete.gender
            var Prova = this.state.athleteListStatus.competition.name
            var Posicao_Escalao = this.state.ListGlobal[this.state.ListGlobal.length - 1].PositionTier
            var Escalao = encodeURIComponent(this.state.athleteListStatus.category)
            var Posicao_Geral = this.state.ListGlobal[this.state.ListGlobal.length - 1].PositionGlobal
            var Tempo = this.state.ListGlobal[this.state.ListGlobal.length - 1].acumulated_time
            var Peitoral = this.state.athleteListStatus.dorsal_number
            var Clube = encodeURIComponent(this.state.athleteListStatus.club)

            var Total_Info_Recebida = ID_Template + Nome_Atleta + Sexo + Prova + Posicao_Geral + Escalao + Posicao_Escalao + Tempo + Peitoral + Clube + Codigo_Validacao;

            var md5 = require('md5');
            var Hash_Calculada = md5(Total_Info_Recebida)
            var url = `${MICROSERVICE_PDFS}/${ID_Template}/${Nome_Atleta}/${Sexo}/${Prova}/${Posicao_Geral}/${Escalao}/${Posicao_Escalao}/${Tempo}/${Peitoral}/${Clube}/${Hash_Calculada}`

            window.location.replace(url);

        }
    }



    render() {
        return (
            <div>
                <Header eventID={this.props.match.params.EventID} />
                {/*Send the event id to the MENU, to get the competition list*/}
                <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
                <div className="content-wrapper">
                    <section class="content-header" >
                        {/*Check if data is loading*/}
                        {(() => {
                            if (this.state.loadingCheckpoint === false) {
                                return (
                                    <section className="content">

                                        <div className="row">
                                            <div className="col-xs-12">
                                                <div className="box">
                                                    {/* /.box-body */}
                                                    <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                                                        <Loader />
                                                    </div>

                                                </div>
                                            </div>
                                        </div>
                                    </section>
                                )
                            }
                            else {
                                return (
                                    <section className="content">
                                        <div className="row">
                                            {/* widget for the number of participants */}
                                            {this.PositionWidgetRender()}
                                            {this.TimeVelocityWidgetRender()}
                                        </div>
                                        <div className="row">
                                            <div className="col-md-3">
                                                {/* Profile Image */}
                                                <div className="box box-primary">
                                                    <div className="box-body box-profile">
                                                        <img className="profile-user-img img-responsive img-circle" src={!!(this.state.athleteListStatus.athlete) ? this.state.athleteListStatus.athlete.photo_url : ""} alt="User profile picture" />
                                                        {/* NEED TO CHECK IF IS UNDEFINED OR IT WILL CRASH*/}
                                                        <h3 className="profile-username text-center"> {!!(this.state.athleteListStatus.athlete) ? this.state.athleteListStatus.athlete.name : ""}</h3>
                                                        {this.getStatus()}
                                                        <ul className="list-group list-group-unbordered">
                                                            <li className="list-group-item">
                                                                <b>< FormattedMessage id="DORSAL" /></b> <div className="pull-right">{this.state.athleteListStatus.dorsal_number}</div>
                                                            </li>
                                                            <li className="list-group-item">
                                                                <b>< FormattedMessage id="COUNTRY" /></b> <div className="pull-right"> {!!(this.state.athleteListStatus.athlete) ? this.state.athleteListStatus.athlete.nacionality : ""}</div>
                                                            </li>

                                                            <li className="list-group-item">
                                                                <b>< FormattedMessage id="CATEGORY" /></b> <div className="pull-right">{this.state.athleteListStatus.category}</div>
                                                            </li>
                                                            <li className="list-group-item">
                                                                <p style={{ textAlign: "right" }}>
                                                                    <div className="pull-left"><b>< FormattedMessage id="TEAM" /></b> </div> {!!(this.state.athleteListStatus.club) ? this.state.athleteListStatus.club : ""}
                                                                </p>
                                                            </li>
                                                            <li className="list-group-item">
                                                                <b>< FormattedMessage id="COMPETITION" /></b> <div className="pull-right"> {!!(this.state.athleteListStatus.competition) ? this.state.athleteListStatus.competition.name : ""}</div>
                                                            </li>
                                                        </ul>
                                                        <div>
                                                            <FavoriteChecker ListaFavoritos={this.props.match.params.AthleteID} AthleteDetail={true} />
                                                        </div>
                                                        <div>

                                                            {this.showCertificateButton()}
                                                        </div>


                                                    </div>
                                                    {/* /.box-body */}
                                                </div>
                                                {/* /.box */}
                                                {/* /.col (LEFT) */}

                                            </div>
                                            {/* /.col */}
                                            <div className="col-md-9">
                                                <div className="box box-primary">
                                                    <ul className="nav nav-tabs">
                                                    </ul>
                                                    <div className="tab-content">
                                                        <div className="active tab-pane" id="Global">
                                                            {/* Post */}
                                                            <div className="post">
                                                                {/* /.user-block */}

                                                                {this.tableGlobal()}


                                                            </div>
                                                            {/* /.post */}
                                                        </div>
                                                        {/* /.tab-pane */}

                                                        {/* /.tab-pane */}

                                                        {/* /.tab-pane */}
                                                    </div>
                                                    {/* /.tab-content */}
                                                </div>
                                                {/* /.nav-tabs-custom */}
                                            </div>

                                            {/* LINE CHART */}
                                            {this.chartsRender()}

                                        </div>
                                        {/* /.row */}
                                    </section>

                                )
                            }
                        })()}
                    </section>
                </div>
                <Footer eventID={this.props.match.params.EventID} />
            </div>
        )
    }
}