import React, { Component } from 'react'
import NavBar from "../structure/HeaderHome";
import FooterPage from "../structure/footerHome";
export default class Erro404 extends Component {
    render() {
        return (
            <div>
                <NavBar />
                    {/* Content Header (Page header) */}

                    {/* Main content */}
                    <section className="content" style={{height:"80vh"}}>
                        <div className="error-page">
                            <h2 className="headline text-yellow"> 404</h2>
                            <div className="error-content">
                                <h3><i className="fa fa-warning text-yellow" /> Oops! Page not found.</h3>
                                <p>
                                    We could not find the page you were looking for.</p>

                            </div>
                            {/* /.error-content */}
                        </div>
                        {/* /.error-page */}
                    </section>

                <FooterPage />
            </div>
        )
    }
}
