import React, { Component } from 'react'
import { Line } from 'react-chartjs-2';
import { FormattedMessage } from 'react-intl';

var data;

export default class Velocity extends Component {

  chartDataVerification() {

    if (!!(this.props.Faster) && !!(this.props.Normal)) {
      return (<Line data={data} />)

    }
    else {
      return (<p><FormattedMessage id="NODATA" /></p>)


    }
  }




  render() {
    if (!!(this.props.Normal) && !!(this.props.Faster)) {
      var position = []
      this.props.checkpoint.map(data => { position.push(data.local) })
      data = {
        labels: (position.length > 2) ? position.slice(1) : position.Normal,
        datasets: [
          {
            label: this.props.Name,
            fill: false,
            lineTension: 0.1,
            backgroundColor: '#00c0ef',
            borderColor: '#00c0ef',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: 'black',
            pointBackgroundColor: '#00c0ef',
            pointBorderWidth: 1,
            borderWidth: 5,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: '#00c0ef',
            pointHoverBorderColor: '#00c0ef',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Normal.length > 2) ? this.props.Normal.slice(1) : this.props.Normal
          },
          {
            label: this.props.LanguageFaster,
            fill: false,
            lineTension: 0.1,
            backgroundColor: '#dd4b39',
            borderColor: '#dd4b39',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: 'r#dd4b39',
            pointBackgroundColor: '#dd4b39',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: '#dd4b39',
            pointHoverBorderColor: '#dd4b39',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Faster.length > 2) ? this.props.Faster.slice(1) : this.props.Faster
          },
          {
            label: this.props.LanguageAverage,
            fill: false,
            lineTension: 0.1,
            backgroundColor: 'rgb(63, 81, 181)',
            borderColor: 'rgb(63, 81, 181)',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: 'rgb(63, 81, 181)',
            pointBackgroundColor: 'rgb(63, 81, 181)',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: 'rgb(63, 81, 181)',
            pointHoverBorderColor: 'rgb(63, 81, 181)',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Average.length > 2) ? this.props.Average.slice(1) : this.props.Average
          },
          {
            label: this.props.LanguageSlow,
            fill: false,
            lineTension: 0.1,
            backgroundColor: '#009688',
            borderColor: '#009688',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: '#009688',
            pointBackgroundColor: '#009688',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: '#009688',
            pointHoverBorderColor: '#009688',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Slow.length > 2) ? this.props.Slow.slice(1) : this.props.Slow
          },

        ]

      };

    }



    return (

        this.chartDataVerification()

    )
  }
}
